.calculator{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(50px, auto);
    font-size: 40px;
    width: 400px;
    margin: 0 auto;
}

.display {
    grid-column: 1/5;
    background-color: #333;
    color: white;
}

.btn{
    background-color: #eee;
    border: 1px solid #999;
}

.operator{
    background-color: orange;
    color: white;
}

.zero{
    grid-column: 1/4;
}
